import React, { useEffect, useState } from "react";
import "./windowsLoading.scss";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../reducers";
import { LoadFiles } from "../../actions/driveActions";
import File from "../../models/File";
import Desktop from "../desktop/Desktop.component";
import WelcomeScreen from "./welcomeScreen.component";
import { MoonLoader } from "react-spinners";

import desktopBgImg from "../../media/winxpbg.jpg";
import welcomeScreenBgImg from "../../media/win10bg.jpg";

const SystemInitializer = (props: any) => {
    const dispatch = useDispatch();
    const drive: File[] = useSelector((state: RootState) => state.driveReducer);

    const [state, setState] = useState({
        isLoaded: false,
        images: InitImages(),
    });

    useEffect(() => {
        dispatch(LoadFiles());
    }, []);

    useEffect(() => {
        if (drive.length > 0 && !state.isLoaded) {
            CheckIfImagesLoaded();
        }
    }, [drive]);

    function InitImages() {
        let desktopImg = new Image();
        desktopImg.src = desktopBgImg;
        let loginImg = new Image();
        loginImg.src = welcomeScreenBgImg;
        return {
            desktopImg,
            loginImg,
        };
    }

    function CheckIfImagesLoaded() {
        let loaded = true;

        loaded =
            state.images.desktopImg.complete && state.images.loginImg.complete;

        for (let file of drive) {
            if (!file.icon.complete) {
                loaded = false;
                break;
            }
        }

        loaded
            ? setState({ ...state, isLoaded: true })
            : setTimeout(() => {
                  CheckIfImagesLoaded();
              }, 1000);
    }

    if (state.isLoaded) {
        return <WelcomeScreen images={state.images} />;
        // return <Desktop background={state.images.desktopImg}/>;
    } else {
        return (
            <div className="winLoading">
                <div className="winLoading__logo">
                    <i className="fab fa-windows"></i>
                </div>
                <MoonLoader color={"white"} size={25} />
            </div>
        );
    }
};

export default SystemInitializer;
